import Contact from "./contact";
import * as React from "react";
import axios from "axios";
import { useDispatch } from "react-redux";
import { useSelector } from "react-redux";

export default function Contacts(props) {
  const [contacts, setContacts] = React.useState([{}]);
  const dispatch = useDispatch();
  const user = useSelector((state) => state.user);

  const message = { myUserName: props.username };

  React.useEffect(() => {
    // code to execute on mount, update, or unmount
    axios
      .post("https://server5-7iue.onrender.com/getContacts", message)
      .then((response) => {
        console.log("contacts :", response);
        setContacts(response.data);
        dispatch({ type: "UP_C", contData: response });
      })
      .catch((error) => {
        console.error(error);
      });
    console.log(user);
  }, []);

  return (
    <div className="Contacts">
      {contacts.map((e) => {
        return <Contact key={e._id} peerName={e.userName} email={e._id} />;
      })}
    </div>
  );
}
