import { useState, useEffect } from "react";
import axios from "axios";

import Text from "./text";

export default function ChatBody(props) {
  const [messages, setMessages] = useState([]);

  useEffect(() => {
    var message = {
      customTableName: props.tableName
    };
    console.log("tableName", props);
    // post data
    const intervalId = setInterval(() => {
      axios
        .post("https://server5-7iue.onrender.com/getMessages", message)
        .then((res) => {
          setMessages(res.data);
          console.log(res.data);
        })
        .catch((err) => {
          console.log(err);
        });
    }, 10000);
    return () => clearInterval(intervalId);
  }, []);

  return (
    <div className="ChatBody" maxWidth="xs">
      {messages.map((mess) => {
        var myText = false;
        if (mess.sender === props.myusername) {
          myText = true;
        } else {
          myText = false;
        }
        return <Text message={mess.message} mine={myText} />;
      })}

      {/* {messages.map((e) => {
        return <Text message={e.message}  mine={false}/>;
      })} */}
    </div>
  );
}
