import BottomInput from "./BottomInput";
import TopProfile from "./TopProfile";
import ChatBody from "./chatBody";
import { useSelector } from "react-redux";

export default function Chat() {
  const selected = useSelector((state) => state);
  console.log("selected", selected.pairedContact);
  return (
    <div className="Chat" maxWidth="xs">
      <TopProfile username={selected.pairedContact.userName} />
      <ChatBody
        tableName={selected.pairedContact.chatListTable}
        myusername={selected.pairedContact.userName}
      />
      <BottomInput info={selected} />
    </div>
  );
}
