import { Avatar, Button, AppBar, Toolbar } from "@mui/material";
import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";
import { useNavigate } from "react-router-dom";

export default function TopProfile(props) {
  const navigate = useNavigate();

  const prev = () => {
    navigate("/home");
  };

  return (
    <div className="TopProfile">
      <AppBar variant="contained" className="TopChild">
        <Toolbar>
          <Button
            onClick={prev}
            style={{ color: "white", height: "10vh", paddingLeft: "0" }}
          >
            <ArrowBackIosIcon />
          </Button>
          <Avatar
            alt="Remy Sharp"
            sx={{ bgcolor: "blue", width: 70, height: 70 }}
          >
            <h2>{props.username.charAt(0)}</h2>
          </Avatar>
          <div className="pContText">
            <h3>{props.username}</h3>
          </div>
        </Toolbar>
      </AppBar>
    </div>
  );
}
