// const axios = require("axios");
const initialState = {
  user: {},
  loggedIn: false,
  myContacts: [{}],
  pairedContact: {}
};

function tableSelector(reciever, cont) {
  const myCont = cont;

  for (var i = 0; i < myCont.length; i++) {
    if (cont[i].userName) {
      if (cont[i].userName === reciever) {
        return cont[i];
      }
    } else {
      continue;
    }
  }
  return myCont[0];
}

function reducer(state = initialState, action) {
  switch (action.type) {
    case "USER_INSERT":
      return {
        ...state,
        user: action.userData,
        loggedIn: true
      };
    case "UP_C":
      return {
        ...state,
        myContacts: action.contData
      };
    case "PEER_REGISTER":
      const customPair = tableSelector(action.peer, state.myContacts.data);
      return {
        ...state,
        pairedContact: customPair
      };
    default:
      return state;
  }
}

export default reducer;
